
*{
	margin: 0;
	padding: 0;
	box-sizing:border-box;
	-moz-box-sizing:border-box; 
	-webkit-box-sizing:border-box;
}
body,html{
	width: 100%;
	height: 100%;
}
#app {
 width: 100%;
 height: 100%;
 background: #f5f5f5;
}
ul, li, a, h2 {
  margin: 0;
  padding: 0;
}
li {
  list-style: none;
}
a {
  text-decoration: none;
}



