
.my-scroller .pull-to-refresh-layer .spinner-holder {
  visibility: hidden;
}
.my-scroller .pull-to-refresh-layer {
  background-image: url(http://qianka.b0.upaiyun.com/images/4f013b6bc7d96fc347f416ad3673f937.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: .8rem .8rem;
  opacity: 0;
  -webkit-transform: scale(1);
  transform: scale(1);
  transition: all .15s linear;
  -webkit-transition: all .15s linear;
}
.my-scroller .pull-to-refresh-layer.active {
  -webkit-transform: scale(1.35);
  transform: scale(1.35);
  opacity: 1;
}
.type {
  width: 1.68rem;
  position: absolute;
  top: 0.9rem;
  bottom: 1rem;
  overflow-y: auto;
}
.type ul {
    width: 100%;
    float: left;
    background: #f5f5f5;
}
.type ul li {
      width: 100%;
      padding: 0 0.2rem;
}
.type ul li a {
        float: left;
        width: 100%;
        text-align: center;
        border-bottom: 1px solid #eeeeee;
        font-size: 0.26rem;
        color: #999998;
}
.type ul li a span:nth-child(1) {
          display: inline-block;
          width: 0.44rem;
          height: 0.44rem;
          opacity: 0.5;
          margin-top: 0.2rem;
}
.type ul li a span:nth-child(1) img {
            width: 100%;
            height: 100%;
}
.type ul li a span:nth-child(2) {
          display: inline-block;
          width: 100%;
          text-align: center;
          padding-bottom: 0.2rem;
}
.type ul li .router-link-exact-active {
        border-bottom: 2px solid #ed5564;
}
.type ul li .router-link-exact-active span:nth-child(1) {
          opacity: 1;
}
.type ul li .router-link-exact-active span:nth-child(2) {
          color: #ed5564;
          font-weight: bold;
}



